.blog-area {
    padding: 130px 0 128px;
  
}
.blog__item {
    visibility: hidden;
    width: 370px;
    margin-bottom: 68px;
    & > a {
        display: block;
    }
    &:hover {
        .blog__item__thumb {
            img {
                @include transform(scale(1.1));
            }   
        }
    }
}
.blog__item__thumb {
    overflow: hidden;
}
.blog__item__info {
    span {
        display: inline-block;
        color: $dusty-gray;
        margin:18px 28px 20px 0;
        i {
            margin-right: 5px;
        }
    }
}

.blog__load-more {
    text-align: center;
    a {
        @include LinkBtn();
    }
    @include HandDrawCir(blog__load-more__btn,150px,-50px,-30px);
}



@include MQ(XL){
    .blog-area {
        padding: 60px 0 60px;
    }
    .blog__item {
        width: 31.2%;
        margin-bottom: 40px;
    }
    .blog__item__info {
        span {
            margin:10px 15px 15px 0;
        }
    }
}

@include MQ(L){
    .blog__item {
        width: 47.8%;
        margin-bottom: 40px;
    }
}


@media (max-width:767px){
    .blog-area {
        padding: 40px 0 40px;
    }
    .blog__item {
        width: 70%;
        left: 50% !important;
        transform: translate(-50%,0);
    }
    .blog__item__info {
        span {
            margin:10px 15px 10px 0;
            font-size: 13px;
        }
        h5 {
            font-size: 18px;
        }
        p {
            font-size: 14px;
        }
    }
}

@media (max-width:370px) {
    .blog__item {
        width: 100%;
    }
}