.erropage {

    background-color: $main-dark;
    position: fixed;
    left: 0;
    top:0;
    width: 100%;
    height: 100%;
    .body {
        font-family: 'Acme';
        margin-top: 300px;
        
        &::before {
            font-weight: 400;
            font-size: 150px;
            line-height: 100px;
            margin-bottom: 35px;
        }
        p {
            font-size: 28px;
            color: $white;
            font-family: $font-second;
    
        }

    }
    .backBtn {
        margin-top: 40px;
        @include BtnSolid();
     
    }
}
.erropage-area {
  padding-top: 60px;
  text-align: center;
  position: absolute;
  top: 45%;
  width: 100%;
  @include transform(translate(0,-50%));
}

.head {
    display: block;
    position: relative;
    width: 400px;
    margin-left: auto;
    margin-right: auto;
    -webkit-animation: shvr 0.2s infinite;
    animation: shvr 0.2s infinite;
  }
  .head::after {
    content: '';
    width: 35px;
    height: 35px;
    background: #000;
    position: absolute;
    top: 60px;
    left: 70px;
    border-radius: 50%;
    @include boxShadow(220px 0 0 #000);
    @include animation(animation,eye 2.5s infinite);

  }
  
  .meta {
    float: left;
    position: relative;
    display: block;
    background: #fff;
    width: 160px;
    height: 170px;
    border-radius: 63% 37% 58% 42% / 49% 36% 64% 51%;
  }
  .meta::after {
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 15px;
    border-radius: 63% 37% 58% 42% / 49% 36% 64% 51%;
    border-bottom: 5px solid $white;

  }
  .meta::before {
    content: '';
    width: 70%;
    height: 100%;
    bottom: 180px;
    left: -20px;
    position: absolute;
    @include transform(rotate(-20deg));
    border-radius: 0% 100% 44% 56% / 92% 86% 14% 8%  ;
    border-bottom: 10px solid $white;

  }
  .meta:nth-of-type(2) {
    float: right;
    -webkit-transform: scaleX(-1);
    transform:scaleX(-1);
  }
  .meta:nth-of-type(2)::after {
    left: 5px;
  }
  .meta:nth-of-type(3) {
    display: none;
  }
  
  .body {
    margin-top: 100px;
    text-align: center;
    color: #fff;
  }
  .body::before {
    content: '404';
    font-size: 80px;
    font-weight: 800;
    display: block;
    margin-bottom: 10px;
  }

  @include keyframe(eye){
    0% ,30% , 55%, 90% , 100% {
      -webkit-transform: translate(0, 0);
      transform: translate(0, 0);
    }
    10%, 25% {
      -webkit-transform: translate(0, 20px);
      transform: translate(0, 20px);
    }
    65% {
      -webkit-transform: translate(-20px, 0);
      transform: translate(-20px, 0);
    }
    80% {
      -webkit-transform: translate(20px, 0);
      transform: translate(20px, 0);
    }
  }
  @include keyframe(shvr){
    0% {
      -webkit-transform: translate(1px, 1em);
      transform: translate(1px, 1em);
    }
    50% {
      -webkit-transform: translate(0, 1em);
      transform: translate(0, 1em);
    }
    100% {
      -webkit-transform: translate(-1px, 1em);
      transform: translate(-1px, 1em);
    }
  }


  @media(max-width:$xl){
    .erropage-area {
      top: 40%;
    }
  }
  @media(max-width:$sm) {
      .body {
          margin-top: 200px !important;
          &::before {
              margin-bottom: 10px !important;
              font-size: 100px !important;
          }
          p {
              font-size: 22px !important;
          }
  
      }
      .backBtn {
          margin-top: 0 !important;
      }
      .meta {
        width: 120px;
        height: 130px;
        &::before {
          bottom: 140px;
        }
      }
      .head {
        width: 290px;
        &::after{
          left: 50px;
          top: 40px;
          @include boxShadow(155px 0 0 #000);
        }
      }
  }