
body {
	cursor: none;
}
.cursor {
	pointer-events: none;
	position: fixed;
	width: 5px;
	height: 5px;
	background-color:$main-dark;
	border-radius: 50%;
    mix-blend-mode: difference;
    z-index: 999;
}
.cursor-follower {
	pointer-events: none;
	position: fixed;
	height: 30px;
	width: 30px;
	border: 2px solid $main-dark;
	border-radius: 50%;
    z-index: 999;
}
.cursor-white {
    background-color:$white;
}
.cursor-folow-white {
    border: 2px solid $white ;
}

// cursor trasnform
.cursor-folow-transform {
    border: unset;
    background-color: rgba($color:$dusty-gray, $alpha: 0.3); 
}
.cursor-transform {
    background-color: transparent;
}
//portdolio-2 turn to categories
.remove-trans {
	background-color: transparent !important;	
}
