/* josefin-sans-300 - latin */

@font-face {
    font-family: 'JosefinSans-300';
    font-style: normal;
    src: url('fonts/josefin/josefin-sans-v14-latin-300.eot'); /* IE9 Compat Modes */
    src: local('Josefin Sans Light'), local('JosefinSans-Light'),
         url('fonts/josefin/josefin-sans-v14-latin-300.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
         url('fonts/josefin/josefin-sans-v14-latin-300.woff2') format('woff2'), /* Super Modern Browsers */
         url('fonts/josefin/josefin-sans-v14-latin-300.woff') format('woff'), /* Modern Browsers */
         url('fonts/josefin/josefin-sans-v14-latin-300.ttf') format('truetype'), /* Safari, Android, iOS */
         url('fonts/josefin/josefin-sans-v14-latin-300.svg#JosefinSans') format('svg'); /* Legacy iOS */
  }
