@include sectionTittle(testi-1__tittle ,125px 0 75px 0,center);

.testi-1__item {
    margin-bottom: 50px;
    display: flex;
    justify-content: space-between;
    h5 {
        @include fontface($font-second-bold,20px,$main-dark,10px 0 15px);
        line-height: 27px;
    }

    &__text {
        padding-top: 10px;
        max-width: 250px;
    }
    @include HandDrawCir(link-draw__hover,120px,-40px,-20px);
    a {
        @include LinkBtn();
        color: #bfbfbf;
        &::before {
            transition: 0.3s ease-in-out;
            border-bottom: 1.5px solid #bfbfbf;
        }
        &:hover {
            color: $main-dark;
            &::before {
                border-bottom: 1.5px solid $main-dark;
            }
        }
    }
}

.testimonial-area {
    position: relative;
    margin-top: 100px;
    @media (max-width:$xl){
        margin-top: 30px;
    }
}
.testimonial {
    &__qoute {
        width: calc(100% + 30px);
    }
    &__text {
        padding: 70px 0 0 70px;
        p {
            font-size: 20px;
        } 
        &__author {
            margin-top: 25px;
            display: inline-flex;
            hr{
                margin: 17px 20px 17px 0;
                width: 80px;
                height: 2px;
                background-color: $main-dark;
            }
            h5 {
                font-size: 25px;
            }
        }
    }
    &__arrow {
       position: absolute;
       right: 0;
       bottom: 80px;
        h6 {
            transition: all 0.3s ease-in-out;
            margin-left: 30px;
            font-family: $font-second-semibold;
            color: #cccccc;
            display: inline-block;
            font-size: 20px;
            .fa-angle-left {
                margin-right: 10px;
            }
            .fa-angle-right {
                margin-left: 10px;
            }
            &:hover {
                color: $main-dark;
            }
        }
    }
}

@media(max-width:$xl){
    .testimonial__qoute {
        width: 100%;
    }
    .testimonial__text {
        padding: 0;
    }
    .testimonial__arrow {
        bottom: 30px;
    }
}

@media(max-width:$lg){
    .testimonial__arrow {
        bottom: 10px;
    }
}
@media(max-width:$md){
    .testimonial__qoute{
        img {
            max-width: 80px;
            margin: 0 auto 15px auto;
        }
    }
}
@media (max-width:$sm){
    .testi-1__item {
        width: 80%;
        margin-left: auto;
        margin-right: auto;
        flex-direction: column-reverse;
    }
    .testimonial-area {
        margin-top: 0;
    }
    .testimonial__text {
        p {
            font-size: 18px;
        }
        &__author {
            margin-top: 15px;
        }
        h5 {
            font-size: 20px;
        }
    }
    .testimonial__arrow {
        top: 25px;
        width: 100%;
        display: flex;
        justify-content: space-between;
        h6 {
            margin: 0;
        }
    }
}