.speaker-slider__tittle {
    text-align: center;
    margin: 100px 0 80px;
}
.speaker-slider-area {
    position: relative;  
}
.speaker-slider__arrow {
    opacity: 0;
    position: absolute;
    right: 0;
    font-size: 25px;
    z-index: 1;
    transition: all 0.5s ease;
    width: 35px;
    height: 35px;
    border-radius: 50%;
    border:1.5px solid transparent;
    text-align: center;
    
    &--up {
        top: 70px;
        line-height: 30px;
    }
    &--down {
        top: 120px;
        line-height: 32px;
    }
    &:hover {
        border:1.5px solid $main-dark;
    }
}

.speaker-slider {

    &__item {
        display: flex !important;
        width: calc(100% + 30px ) !important;
        margin: 0 -15px;
        & > div {
            opacity: 0;
            &:nth-child(1) {
                padding-right: 35px;
                margin-top: 105px;
            }
            &:nth-child(3) {
                padding-left: 35px;
                margin-top: 230px;
            }
        }
        .thumb-float-down {
            @include animation(animation,FloatDown 1s);
        }
        @include keyframe(FloatDown) {
            0% {
              @include transform(translate(0,0px));
            }
            100% {
                @include transform(translate(0,200px));
            }
        }
    }

    
}
.speaker-slider__item__thumb {
    position: relative;
    &:hover {
        .speaker-slider__item__thumb__info {
            -webkit-clip-path: circle(100% at 50% 50%);
            clip-path: circle(100% at 50% 50%);
        }
    }
    &__info {
        transition: clip-path 0.6s ease-in;
        position: absolute;
        top: 0;
        left: 0;
        background-color: $white;
        width: 100%;
        height: 100%;
        @include Center();
        flex-direction: column;
        -webkit-clip-path: circle(0% at 50% 50%);
        clip-path: circle(0% at 50% 50%);
        h4 {
            font-size: 30px;
        }
        h5 {
            font-size: 22px;
        }
    }

    height: 420px;
    display: block;
    img {
        height: 100%;
        width: 100%;
        object-fit: cover;
        @include filter(grayscale(100%));
    }
}


@media(max-width:$xl){
    .speaker-slider__tittle {
        h1 {
            font-size: 70px ;
           
        }
        margin: 60px 0 60px;
    }
    .speaker-slider {
        &__item {

            & > div {
                &:nth-child(1) {
                    padding-right: 15px;
                    margin-top: 65px;
                }
                &:nth-child(3) {
                    padding-left: 15px;
                    margin-top: 160px;
                }
            }
        }
    
    }
    .speaker-slider__arrow {
        &--up {
            top: 50px;
        }
        &--down {
            top: 100px;
        }
    }
    .speaker-slider__item__thumb {
        height: 350px;
    }
}

@media(max-width:991px) {
    .speaker-slider__tittle {
        h1 {
            font-size: 50px ;
        }
        margin: 40px 0 40px;
    }
    .speaker-slider__arrow {
        &--up {
            top: 0px;
        }
        &--down {
            top: 40px;
        }
    }
    .speaker-slider {
        .speaker-slider__item {
            & > div {
      
                &:nth-child(1) {
                    margin-top: 0;
                }
                &:nth-child(2) {
                    margin-top: 80px;
                    
                }
                &:nth-child(3) {
                    display: none;
                }
            }
        }
    }

}

@media(max-width:767px) {

    .speaker-slider__item {

        & > div {
            &:nth-child(1) {
                margin: 0 auto ;
                width: 70%;
            }
            &:nth-child(2) {
                margin-top: 80px;
                display: none;
            }
        }
    }
}

@media(max-width:$sm) {
    .speaker-slider__tittle {
        h1 {
            font-size: 35px ;
            line-height: 35px;
        }
    }
    .speaker-slider {
        .speaker-slider__item {
            & > div {
                &:nth-child(1) {
                    width: 320px;
                }
            }
        }
    }
}
@media(max-width:400px) {

    .speaker-slider {
        .speaker-slider__item {
            &__thumb {
                height: 320px;
            }
            & > div {
                &:nth-child(1) {
                    width: 85%;
                }
            }
        }
    }

}

