/* acme-regular - latin */
@font-face {
    font-family: 'Acme';
    font-style: normal;
    font-weight: 400;
    src: url('fonts/acme/acme-v9-latin-regular.eot'); /* IE9 Compat Modes */
    src: local('Acme Regular'), local('Acme-Regular'),
         url('fonts/acme/acme-v9-latin-regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
         url('fonts/acme/acme-v9-latin-regular.woff2') format('woff2'), /* Super Modern Browsers */
         url('fonts/acme/acme-v9-latin-regular.woff') format('woff'), /* Modern Browsers */
         url('fonts/acme/acme-v9-latin-regular.ttf') format('truetype'), /* Safari, Android, iOS */
         url('fonts/acme/acme-v9-latin-regular.svg#Acme') format('svg'); /* Legacy iOS */
  }