@include sectionTittle(venue-layout__tittle,160px 0 160px 0,center);
.venue-fadeinup {
    opacity: 0;
}
.venue-video {
    p {
        font-size: 22px;
    }
    &__iframe {
 
        height: 480px; 
        video {
      
            width: 100%;
            height: 100%;
            @include filter(grayscale(100%));

        }
        
    }

    &__text {
    
        padding-top: 30px;
        height: 100%;
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        &__tittle {
            h2 {
                font-size: 50px;
                &:nth-child(2){
                    font-family: $font-primary;
                }
            }
            text-align: right;
        }

        &__address {
            margin-top: 30px;
            transition: all 0.3s ease;
            i {
                font-size: 70px;
                margin-right: 30px;
            }
            &__detail {
                display: flex;
                align-items: center;
            }
            p {
                color: $main-dark;
                font-family: $font-primary-semibold;
            }
            &:hover {
                iframe {
                    height: 230px;
                }
            }
        }
        iframe {
            width: 100%;
            height: 0; 
            border-width: 0px;
            transition: all 0.5s ease;
            @include filter(grayscale(100%));
        }
    }
}

.venue-layout-grid {
    padding-top: 150px;

    &__item {
        &:nth-child(1){
            margin-top: 0;
        }
        width: 50%;
        margin-top: 135px;
        &__thumb {
            transition: all 0.3s ease;
            @include filter(grayscale(100%));
            margin-bottom: 50px;
            overflow: hidden;
        }
        h3 {
            font-family: $font-primary-bold;
            margin-bottom: 10px;
        }
        &__content:hover {
            .venue-layout-grid__item__thumb {
                @include filter(grayscale(0%));
                img {
                    @include transform(scale(1.1));
                    
                }
            }
        }
    }
}

@media (min-width:1752px){
    .venue-video {
        &__iframe {
            padding-right: 92px;
            height: 650px; 

        }

        &__text {
        
            width: calc(100% + 20px);
            margin-left: -20px;
        }
    }

    .venue-layout-grid {

        &__item {
            &__content {
                display: block;
            }
            
            &:nth-child(1){
           
                .venue-layout-grid__item__content {
                    width: 570px;
                    float: right;
                }
            }
            &:nth-child(2){
         
                .venue-layout-grid__item__content {
                    width: 575px;
                    float: right;
                }  
            }
            &:nth-child(3){
   
                .venue-layout-grid__item__content {
                    width: 645px;
                    float: left;
                } 
            }
            &:nth-child(4){
                .venue-layout-grid__item__content {
                    width: 790px;
                    float: left;
                } 
            }
        }
    }
}

@media (max-width:1752px) {
    .venue-video {
        p {
            font-size: 15px;
        }
    }

    .venue-video__text {
        &__tittle {
            h2 {
                font-size: 30px;
            }
        }
        i {
            font-size: 40px;
        }
    }
    .venue-layout-grid {
        padding-top: 150px;
        &__item {
            &__content {
                display: block;
                width: 90%;
            }

            &:nth-child(1){
                .venue-layout-grid__item__content {
 
                    float: right;
                }
            }
            &:nth-child(2){
                .venue-layout-grid__item__content {
             
                    float: right;
                }  
            }
            &:nth-child(3){
                .venue-layout-grid__item__content {
              
                    float: left;
                } 
            }
            &:nth-child(4){
                .venue-layout-grid__item__content {
      
                    float: left;
                } 
            }
        }
    }
}

@media (max-width:$xl){
    .venue-video {
        &__iframe {
            height: 400px;
        }
    }
    .venue-layout-grid {
        padding-top: 60px;
        &__item {
            margin-top: 60px;
            h3 {
                font-size: 25px;
            }
            &__thumb {
                margin-bottom: 25px;
            }
        }
    }
}
@media (max-width:$lg){
    .venue-video {
        &__iframe {

            height: 450px; 

        }
    }
}
@media(max-width:$md){
    .venue-layout-grid {
        padding-top: 40px;
    }
}
@media (max-width:$sm){
    .venue-video__text{
        h2 {
            font-size: 25px;
        }
        i {
            margin-right: 20px;
        }
    }

}


@media (max-width:767px){
    .venue-video {
        &__iframe {

            height: 350px; 

        }
    }
    .venue-layout-grid {

        &__item {
            margin-top: 30px;
            left: 50% !important;
            @include transform(translate(-50%,0));
            width:80% ;
            &__content {
                width: 100%;
                float: unset !important;
            }
            &__thumb {
                margin-bottom:10px;
            }
            h3 {
                margin: 0;
                font-size: 20px;
            }
        }
    }
}
@media (max-width:540px){
    .venue-video {
        &__iframe {

            height: 310px; 
            @media (max-width:435px){
                height: 250px; 
            }
            @media(max-width: 400px){
                height: 230px;
            }
            @media(max-width: 375px){
                height: 200px;
            }
        }
    }
}