@include sectionTittle(contact__tittle,155px 0 135px 0,center);
.contact-info {
    h4 {
        font-size: 40px;
        margin-bottom: 27px;
    }
    &__link {
        display: flex;
        img {
            flex-shrink: 0;
            margin-right:30px;
        }
        align-items: center;
        padding: 14px 0;
        text-shadow: 0px 0.5px #adadad;
    }

    &__item-left {
        padding-left: 70px;
    }
}


@include MQ(XL){
    .contact-info__item-left {
        padding-left: 0;
    }
}
@media (max-width:$lg){

    .contact-info__item-left {
        .fillin-form {
            margin-top: 40px;
        }  
        h4 {
            margin-bottom: 15px !important ;
            line-height: 35px;
            font-size: 35px !important;
            text-align: center;
        }
    }
}
@media (max-width:$sm){
    .contact-info__item-left {
  
        h4 {
            font-size: 27px !important;
        }
    }
}
