
@include sectionTittle(portfilio-1__tittle ,120px 0 115px 0,center);
.portfilio-1__second-tittle {
    h2 {
        text-align: center;
        font-size: 60px;
        line-height: 70px;
    }
}
.portfilio-1__category {
    margin: 80px 0 110px;
}
.portfilio-1__category__item {
    margin-bottom: 30px;
    display: flex;
    h2 {
        -webkit-text-stroke-width: 1.5px;
        -webkit-text-stroke-color: #cccccc;
        @include fontface($font-primary-bold,60px,$white,-25px 0 0);
        
    }
    hr {
        width: 50px;
        height: 2px;
        background-color:#cccccc;
        margin: 25px 10px;
    }
    h5 {
        text-transform: uppercase;
        @include fontface($font-second-bold,25px,$main-dark,0 0 15px 0);
        line-height: 30px;
    }
    &__group {
        li {
            list-style-type: none;
            font-family: $font-primary-medium;
            a {
                color: $nd-dark;
                margin-top: 7px;
                display: block;
                &:hover {
                    font-family: $font-primary-semibold;
                }
            }
        }

    }
}

.portfilio-1__item {
    &:hover > .portfilio-1__item__category {    
        -webkit-clip-path: circle(100% at 50% 50%);
        clip-path: circle(100% at 50% 50%);

    }
    position: relative;
    display: block;
    margin-bottom: 30px;
    width: calc(25% - 22.5px);
    &__category {
        transition: all 0.3s ease-in;
        transition: clip-path 0.5s ease-in-out;
        position: absolute;
        @include Center();
        flex-direction: column;
        top:0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: $white;
        h5 {
            font-family: $font-second-bold;
            text-transform: uppercase;
        }
        -webkit-clip-path: circle(0% at 50% 50%);
        clip-path: circle(0% at 50% 50%);
    }
    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    &__2w {
        width: calc(50% - 15px);
    }
}

@media (max-width:$xl){
    .portfilio-1__second-tittle h2 {
        font-size: 50px;
        line-height: 50px;
    }
    .portfilio-1__category {
        margin: 60px 0 30px;
    }
}
@media (max-width:$lg) {
    .portfilio-1__item {
        width:calc(50% - 15px);
        &__2w {
            width: 100%;
        }
    }
    .portfilio-1__second-tittle h2 {
        font-size: 40px;
        line-height: 40px;
    }

}
@media (max-width:$md){
    .portfilio-1__second-tittle h2 {
        font-size: 30px;
        line-height: 30px;
    }
    .portfilio-1__category__item__group {
        h5 {
            font-size: 20px;
        }
    }
    .portfilio-1__category {
        margin: 60px 0 30px;
    }
}
@media (max-width:$sm){
    .portfilio-1__category {
        margin: 40px 0 10px;
    }
    .portfilio-1__category__item h2 {
        font-size: 50px;
        margin-top: -17px;
    }
}
@media (max-width:460px) {
    .portfilio-1__item {
        width:100%;
    }

}

