.menu-nav {

    transition:all 0.3s;

    position: fixed;
    overflow-y: scroll;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 99;
    @include transform(translateX(-100%));
    // -webkit-clip-path: circle(0% at 6% 8%);
    // clip-path: circle(0% at 6% 8%);
    background-color: rgba($color: $main-dark, $alpha: 0.95);
    .header__logo {
        position: absolute;
        left: 50%;
        @include transform(translate(-50%,0));
        margin-left: 0;
        top: 40px;
    }

    nav {
        position: absolute;
        left: 50%;
        top: 150px;
        @include transform(translate(-50%,0%));
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 0;
        ul {
            width: 450px;
            margin: 0;
        }
    }
    .menu-nav-close {
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
    }

    li {
        list-style-type: none;
        position: relative;
        text-align: center;
        width: 100%;
        .sub-menu {
            display: none;
            a {
                font-size: 25px;
            }
            & > li {
                .sub-menu {
                    display: none;
                }
                i {
                    top: 15px;
                    right: 10px;
                }
            }
        }
        i {
            position: absolute;
            transition: all 0.3s;
            top: 25px;
            right: 0;
            color: $white;
            &::before {
                font-size: 25px;
            }
        }
        .arrow-rotation {
            
            @include transform(rotate(180deg));
            
        }
    }

    /* General link styles */
    .menu-link {

        -webkit-text-stroke-width: 1px;
        -webkit-text-stroke-color: $white;
        font-family: $font-second-semibold;
        overflow: hidden;
        outline: none;
        text-decoration: none;
        position: relative;
        font-size: 50px;
        color: transparent ;
        display: inline-block;
    }

    /* Kukuri */

    .link--kukuri{
        transition: margin 0.5s;

        &::after {
            content: '';
            position: absolute;
            height: 5px;
            width: 100%;
            top: 50%;
            margin-top: -3px;
            right: 0;
            background: $white;
            -webkit-transform: translate3d(-101%,0,0);
            transform: translate3d(-101%,0,0);
            -webkit-transition: -webkit-transform 0.4s;
            transition: transform 0.5s;
            -webkit-transition-timing-function: cubic-bezier(0.7,0,0.3,1);
            transition-timing-function: cubic-bezier(0.7,0,0.3,1);
        }

        &::before{
            content: attr(data-letters);
            position: absolute;
       
            overflow: hidden;
            color: $white;
            white-space: nowrap;
            width: 0%;
            -webkit-transition: width 0.4s 0.3s;
            transition: width 0.4s 0.3s;
        }
    }

    .link--kukuri:hover::after {
        -webkit-transform: translate3d(101%,0,0);
        transform: translate3d(101%,0,0);
    }

    .link--kukuri:hover::before {
        width: 100%;
    }

}


.menu-nav-active {
    // -webkit-clip-path:circle(150% at 10% 10%);
    // clip-path: circle(150% at 10% 10%);
    @include transform(translateX(0));
    // @include transform(scaleX(1));
}
.button-eff:not(#header-trans2__btn){
    position: fixed !important;
}
.button-eff {
    z-index: 100;
    span {
        @include boxShadow(0px 0px 0px 0px $white !important);
    }
    &::before {
        width: 15px !important;
        height: 15px !important;
    }
    & + .position-fixed {
        display: block;
    }
}
.position-fixed {
    display: none;
    width: 23px;
}



@media(max-width:$sm){
    .menu-link {
        font-size: 40px !important;
    }
    .menu-nav{
        padding-top: 100px;
        ul {
            width: 270px !important;
        }
    }
}