.blog-single-content {
    h4 {
        margin-bottom: 15px;
    }
    padding: 130px 100px;
    &__thumb {
        height: 650px;
        img {
            height: 100%;
            object-fit: cover;
        }
    }

    &__time {
        margin: 17px 0 40px;
        span {
            font-size: 18px;
            margin-right: 35px;
            color: $dusty-gray;
            i {
                margin-right: 8px;
            }
        }
    }

    &__paragraph {
        p {
            &:first-child {
                margin-top: 0;
            }
            margin-top: 15px;
 
        }
    }


    &__tags {
        display: flex;
        align-items: center;
        span {
            @include fontface($font-second-semibold,18px,$main-dark,0 8px 0 0);
        }
        a {
            @include fontface($font-primary,15px,$dove-gray,0 3px);
            &:hover {
                color: $main-dark;
                font-weight: 600;
            }
        }
    }


}

.blog-single-content__quote {
    margin: 65px 0 50px;
    &__text {
        padding: 30px 0 30px 30px;
        p {
            position: inherit;
        }
    }
    &__main {
        margin-bottom: 43px;
        padding: 43px 40px;
        background-color: #f2f2f2;
        border-left: 6px solid $main-dark;
        p {
            color:$nd-dark;
            font-size: 18px;
        }
         
        
    }
}

.blog-single-content__paging {
    display: flex;
    height: 150px;

    border-top: 1px solid $border;
    border-bottom: 1px solid $border;
    margin: 55px 0;
    &__divide {
        width: 50%;
        height: 100%;
        display: flex;
        align-items: center;
        &--next {   
            text-align: right;
            flex-direction: row-reverse;
            position: relative;
            &::before {
                position: absolute;
                content:"";
                height: 70%;
                width: 0px;
                left: 0;
                border-left: 1px solid $border;
            }
        }
    }
    &__btn {
        span {
            @include fontface($font-second-semibold,18px,$dove-gray,0 0 10px 0);

            .fa-angle-double-left {
                margin-right: 10px;
        
            }
            .fa-angle-double-right {
                margin-left: 10px;
            }
      
            display: inline-block;
        }
        span, h5 {
            transition: all 0.3s;
            color: $dusty-gray;
        }
        &:hover {
            span, h5 {
                color: $main-dark;
            }
        }
    }
}

.blog-single-content__writer {
    border-bottom: 1px solid $border;
    padding: 5px 0 60px;
    display: flex;
    &__avatar {
        width: 120px;
        height: 120px;
        flex-shrink: 0;  
    }
    &__text {
        padding-left: 30px;
        h3 {
            display: inline-block;
            font-size: 15px;
            margin-right: 10px;
        }
        &__role {
            @include fontface($font-primary,14px,$dusty-gray,0 10px);
        }
        p {
            margin: 10px 0 15px;
        }
        a:hover{
            i {
                color: $main-dark !important;
            }
    
        }
        i {
            font-size: 14px;
            color: $dusty-gray;
            margin-right: 15px;
        }
    }
}






@include MQ(XL){
    .blog-single-content {
        padding: 60px 0;
        &__thumb {
            height: 450px;
        }
        &__time {
            margin: 17px 0;  
        }
        h4 {
            font-size: 30px;
        }

        &__quote {
            margin: 30px 0;
        }
        .blog-single-content__quote__text {
            padding: 0;
        }
        .blog-single-content__quote__main {
            padding: 30px;
            margin-bottom: 30px;
        }

    }

}
@media (max-width:$lg){
    .blog-single-content__paging {
        margin: 40px 0;

    }
    .blog-single-content__writer {
        padding: 0 0 40px 0;
    }

}
@include MQ(M) {
    .blog-single-content__quote__main {
        margin-top: 30px;
        padding: 20px !important;
        p {
            font-size: 16px;
        }
    }
    .blog-single-content__paging {
        h5 {
            font-size: 18px;
        }
    }
}

@media (max-width:$sm) {
    .blog-single-content {
        h4 {
            font-size: 22px;
        }
    }
    .blog-single-content__time {
        span {
            font-size: 15px;
            margin-right: 20px;
        }
    }
    .blog-single-content__thumb {
        height: 320px;
    }
    .blog-single-content__writer__avatar {
        width: 80px;
        height: 80px;
    }
    .comment__item__text ,.blog-single-content__writer__text{
        padding-left: 15px !important;
    }
    .blog-single-content__paging {
        h5 {
            font-size: 16px;
        }
    }
    .comment__item__text span {
        display: block;
        margin: 0 !important;
    }
}