.speaker-grid-1 {
    height: 1800px;
    width: 100%;
    &__item {
     

        cursor: pointer;
        overflow: hidden;
        .detail-direction{
            & > img {
                height: 100% ;
                width: 100%;
                object-fit: cover;
                @include filter(grayscale(100%));
                @include transform(scale(1.05));
            }
            &:hover {
                img {
                    @include filter(grayscale(0%)); 
                    @include transform(scale(1));     
                }
            }

        }
        .speaker-infomation {
            display: none;
        }
    }
  
}





@media(min-width:$md){
    .spk {
        &--width1000 {
            width: 52.0833333%;
        }
        &--width460 {
            width: 23.9633333%;
        }
        &--width600 {
            width: 31.25%;
        }
        &--width400 {
            width: 20.833333%;
        }
        &--width520 {
            width: 27.0833333%;
        }
        &--width500 {
            width: 26.04166666%;
        }
    
        &--height {
            &--770 {
                height: 42.7777777%;
            }
            &--600 {
                height: 33.33333333%;
            }
            &--500 {
                height: 27.777777%;
            }
            &--530 {
                height: 29.4444444%;
            }
            &--400 {
                height: 22.222222%;
            }
            &--300 {
                height: 16.66666%;
            }
        }
    }
}
@media (max-width:1500px){
    .speaker-grid-1 {
        height: 1500px ;
    }
}

@media (max-width:$xl){
    .speaker-grid-1 {
        height: 1200px ;
    }
}
@media (max-width:$lg){
    .speaker-grid-1 {
        height:800px ;
    }
}
@media (max-width:767px){
    .speaker-grid-1__item {
        width: 50%;
    }
    .spk {
        &--width1000 {
            width: 100%;
        }
    }
}