.pagebanner-1 {
    height: 810px;
    width: 100%;
    @include brgImg("../images/pagebannerBrg.jpg");

    &__tittle {
        
        margin-left: 130px;
        &__conference {
            @include fontface($font-second-bold,95px,$white,0);
        }
        &__webdesign {
            @include fontface($font-second-light,95px,$white,0);
            line-height: 100px;
        }
        .letter {
            opacity: 0;
        }
    }

    display: flex;
    align-items: center;
}


@media (max-width:1752px) {
    .pagebanner-1__tittle {
        margin-left: 0;
    }
}
@include MQ(XL) {
    .pagebanner-1 {
        height: 600px;
    }
    
    .pagebanner-1__tittle {
        &__conference {
            @include fontface($font-second-bold,75px,$white,0);
        }
        &__webdesign {
            @include fontface($font-second-light,75px,$white,0);
            line-height: 75px;
        }
    }
}


@media(max-width:$lg) {
    .pagebanner-1 {
        height: 400px;
    }
    
    .pagebanner-1__tittle {

        &__conference {
            @include fontface($font-second-bold,55px,$white,0);
        }
        &__webdesign {
            @include fontface($font-second-light,55px,$white,0);

        }
    }
}
@include MQ(M) {
    .pagebanner-1 {
        height: 300px;
    }
    .pagebanner-1__tittle {
        width: 65% !important;
        &__conference {
            @include fontface($font-second-bold,45px,$white,0);
            line-height: 45px;
        }
       
        &__webdesign {
            @include fontface($font-second-light,45px,$white,0);      
        }
    }
}
@include MQ(S) {
    .pagebanner-1 {
        height: 250px;
    }
    .pagebanner-1__tittle {
        &__conference {
            @include fontface($font-second-bold,35px,$white,0);
        }
        &__webdesign {
            @include fontface($font-second-light,35px,$white,0);
        }
    }
}
