// rem fallback - credits: http://zerosixthree.se/

@function calculateRem($size) {
  $remSize: $size / 16px;
  @return $remSize * 1rem;
}

@mixin font-size($size) {
  font-size: $size;
  font-size: calculateRem($size);
}

// border radius

@mixin border-radius($radius: 0.25em) {
  border-radius: $radius;
}

// antialiasing mode font rendering

@mixin font-smoothing {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

// media queries

@mixin MQ($breakpoint) {
  @if $breakpoint == S {
    @media screen and (max-width: $sm) {
      @content;
    }
  } @else if $breakpoint == M {
    @media screen and (max-width: 767px) {
      @content;
    }
  } @else if $breakpoint == L {
    @media screen and (max-width: 990px) {
      @content;
    }
  } @else if $breakpoint == XL {
    @media screen and (max-width: 1199px) {
      @content;
    }
  }
}


//--------------------------------- Project Mixin:

@mixin sectionTittle($tit,$margin,$align) {
  .#{$tit} {
    h1 {
        letter-spacing: -4px;
        text-align: $align;
        -webkit-text-stroke-width: 4px;
        -webkit-text-stroke-color: $main-dark;
        line-height: 130px;
        @include TextFillUp($white,$main-dark);
        @include fontface($font-primary-bold,120px,$white,$margin);
        @media (max-width:$xl){
          line-height: 110px !important;
          @include fontface($font-primary-bold,100px,$white,60px 0 !important);
        }
        @media (max-width:$lg){
          -webkit-text-stroke-width: 2px;
          line-height: 80px !important;
          @include fontface($font-primary-bold,70px,$white,60px 0 !important);
        }
        @media (max-width:$md){
          letter-spacing: -2px !important;
          line-height: 65px !important;
          @include fontface($font-primary-bold,55px,$white,40px 0 !important);
        }
        @media (max-width:$sm){
          letter-spacing: -2px !important;
          line-height: 45px !important;
          -webkit-text-stroke-width: 1px;
          @include fontface($font-primary-bold,35px,$white,40px 0 !important);
        }
    }
    
  }
 
}
@mixin fontface($fontfamily,$fontsize,$color,$margin) 
{
    font-family: $fontfamily;
    font-size: $fontsize;
    color: $color;
    margin: $margin;
}

@mixin brgImg($path) 
{
  background-image: url($path);
  background-position: center;
  background-size: cover;
}

@mixin boxShadow ($artr) {
  -ms-box-shadow: $artr;
  -webkit-box-shadow: $artr;
  -moz-box-shadow: $artr;
  box-shadow: $artr;
  -o-box-shadow: $artr;
  
}
@mixin Rotate($deg) {
  transform: rotate($deg);
-ms-transform: rotate($deg); 
-webkit-transform: rotate($deg); 
-moz-transform:rotate($deg); 
-o-transform:rotate($deg); 
}


@mixin BtnTrans() {
  color: $white;
  font-family: $font-second-semibold;
  display: inline-block;
  text-align: center;
  line-height: 50px;
  background-color: transparent;
  border: 1.5px solid $white;
  height: 45px;
  width: 130px;
  text-transform: uppercase;
  background-image: linear-gradient(
    to right,
    $white,
    $white 50%,
    transparent 50%);
  background-size: 210% 100%;
  background-position: 100%;
  &:hover {
  transition: all 0.5s cubic-bezier(0.000, 0.000, 0.230, 1);
  background-position: 0%;
  span {
      color: $main-dark;
  }
  }
}
@mixin BtnSolid() {
  color: $main-dark;
  font-family: $font-second-semibold;
  display: inline-block;
  text-align: center;
  line-height: 47px;
  background-color: $white;
  border: 1.5px solid $white;
  height: 45px;
  width: 130px;
  text-transform: uppercase;
  background-image: linear-gradient(
    to right,
    $main-dark,
    $main-dark 50%,
    $white 50%);
    background-size: 210% 100%;
    background-position: 99%;
    &:hover {
    transition: all 0.5s cubic-bezier(0.000, 0.000, 0.230, 1);
    background-position: 0%;
    color: $white ;
  }
}
@mixin LinkBtn() {
  position: relative;
  &::before {
    bottom: 3px;
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    border-bottom: 1.5px solid $main-dark;

  }
  color: $main-dark;
  text-transform: uppercase;
  font-family: $font-second-semibold;
  display: inline-block;
  
}

@mixin Center(){
  display: flex;
  align-items: center;
  justify-content: center;
}

@mixin transform($prop) {
  -ms-transform:$prop; /* IE 9 */
  -webkit-transform:$prop; /* Safari 3-8 */
  -moz-transform: $prop;; 
  -o-transform: $prop;; 
  transform: $prop;
}

@mixin filter($prop) {
  -moz-filter:$prop;
  -webkit-filter: $prop;
  filter: $prop;
}
@mixin animation($prop,$move) {
  -webkit-#{$prop}: $move;
  -moz-#{$prop}: $move;
  -ms-#{$prop}:$move;
  -o-#{$prop}: $move;
  #{$prop}: $move;
}
@mixin keyframe($mymove) {
  @keyframes #{$mymove} {
    @content;
  }
  @-moz-keyframes #{$mymove} {
    @content;
  }
  @-webkit-keyframes #{$mymove} {
    @content;
  }
  @-o-keyframes #{$mymove} {
    @content;
  }
}

@mixin BrgFillTop($color) {
  position: relative;
  &::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: $color;
    z-index: -1;
    @include transform(scaleY(0));
    transform-origin: 50% 100%;
    transition-property: transform;
    -webkit-transition: 300ms ease-out;
    transition: 300ms ease-out;
  }
  &:hover {
    &::before {
      @include transform(scaleY(1));
      -webkit-transition-timing-function: cubic-bezier(0.52, 1.64, 0.37, 0.66);
      transition-timing-function: cubic-bezier(0.52, 1.64, 0.37, 0.66);
    }
  }
}

@mixin TextFillUp($main-color,$secondary-color) {
  color: $main-color;
  text-decoration: none;
  -webkit-background-clip: text;
  background-clip:text;
  -webkit-text-fill-color: transparent;
  background-image: linear-gradient(
      0deg,
      $secondary-color,
      $secondary-color 50%,
      $main-color 50%);
      background-size: 100% 200%;
      background-position: 0% 0%;


  &:hover {
  transition: all 0.8s cubic-bezier(0.000, 0.000, 0.230, 1);
  background-position: 100% 100%;
  }

}

@mixin HandDrawCir($contain,$width,$top,$left){
  .#{$contain} {
    display: inline-block;
    position: relative;
    @media(max-width:991px) {
      svg {
        display: none;
      }
    }
    &:hover {
      path {
        stroke:$main-dark;
        @include animation(animation,draw 1s linear alternate forwards);
      }
      
    }
  
    svg {
      position: absolute;
      width: $width;
      top: $top;
      left: $left;
  
      path {
        @include animation(animation,drawReverse 0.5s);
        fill:none;
        stroke:transparent;
        transition: stroke 1s;
        stroke-dasharray: 1000;
        stroke-dashoffset: 1000;
          
      }
      @include keyframe(draw){
        from {
            stroke-dashoffset: 1000;
        }
        to {
        stroke-dashoffset: 0;
        }
      }
      @include keyframe(drawReverse){
        from {
            stroke-dashoffset: 0;
        }
        to {
        stroke-dashoffset: 1000;
        }
      }
    }
  }
}


