@include sectionTittle(sponsor__tittle,115px 0 105px 0,center);
.sponsor {
    margin-bottom: 100px;
    height: 400px;
    display: flex;
    flex-wrap: wrap;
    position: relative;
    &::before {
        position: absolute;
        content: "";
        width: 1px;
        height: 100%;
        border: 2px solid $white;
        z-index: 0;
    }
    &::after {
        position: absolute;
        content: "";
        width: 100%;
        height: 1px;
        border: 2px solid $white;
        z-index: 0;
    }
    &__item {
       
        width: 20%;
        height: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        border-left: 1px solid $border;
        border-top: 1px solid $border;
        img {
            @include transform(scale(0));
        }
        &:hover {
            img {
                @include filter(hue-rotate(0deg) saturate(0%) brightness(0%));
                // @include filter(grayscale(100) invert(1) contrast(0%));
            }
        }
    }
}


@include MQ(XL) {
    .sponsor {
        height: 340px;    
    }
    .sponsor__item {
        img {
            max-width: 80%;
        }
    }
}
@include MQ(L) {
    .sponsor {
        height: 300px;
        margin-bottom: 60px;
    }

}

@media (max-width:767px){
    .sponsor {
        margin-bottom: 40px;
        height: auto;
        &::after {
            border: 2px solid transparent;
        }
    }
    .sponsor__item {
        height: 100px;
        border: unset;
        display: inline-flex !important;
        align-items: center;
        img {
            margin: auto;
        }
    }
}