.fillin-form {
    textarea {
        height: 180px;
        margin-bottom: 47px;
    }
    &__fields {
        display: flex;
        justify-content: space-between;
        input {
            width:calc(50% - 15px);
        }
        margin-bottom: 40px;
    }
    button {
        @include LinkBtn();
        background-color: transparent;
        border: unset;
    }
    @include HandDrawCir(fillin-form__submit,100px,-30px,-30px)
}

@media (max-width:$md){
    .fillin-form {
        &__fields {
            flex-direction: column;
            input {
                width: 100%;
                margin-bottom: 15px;
            }
        }
        .fillin-form__fields {
            margin-bottom: 0;
        }
        textarea {
            margin-bottom: 30px !important;
        }
    }

}