@include sectionTittle(portfolio-3__tittle,150px 0 140px 0,center);
.portfolio-3 {
    padding-bottom: 150px;
    &__grid {
        margin: 65px 0 75px;
    }
    .portfilio-3__item {
        &:hover > .portfilio-3__item__category {    
            -webkit-clip-path: circle(100% at 50% 50%);
            clip-path: circle(100% at 50% 50%);
        }
        position: relative;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
        width: calc(50% - 15px);
        &__nor,&__2h {
            width: calc(25% - 22.5px);
        }
        &__2w {
            width: calc(50% - 15px);
        }
        &__category {
            @extend .portfilio-1__item__category;
        }
    }
    .port3-filter { 
        color: $main-dark;
        &::before {
            width: 100%;
        }
        span {
            opacity: 1;
        }
    }
    ul{
        text-align: center;
        li {
            transition: all 0.3s;
            &::before {
                transition: all 0.3s ease-in-out;
                content: "";
                position: absolute;
                width: 0;
                height: 2px;
                background-color: $dove-gray;
                bottom: 4px;
            }
            position: relative;
            list-style-type: none;
            display: inline-block;
            @include fontface($font-second-semibold,20px,$dusty-gray,0 20px);
            & > span {
                transition: all 0.3s ease-in-out;
                font-size: 12px;
                position: absolute;
                font-family: $font-second-bold;
                top: -8px;
                right: -13px;
                opacity: 0;
            }
            &:hover {
                span {
                    opacity: 1;
                }
                color: $main-dark;
            }
        }
    }
    .portfilio-3__item-sizer {
        width: calc(25% - 22.5px);
    }
    .portfolio__load-all {
        text-align: center;
        @include HandDrawCir(link-draw__hover,140px,-50px,-30px);
        a {
            @include LinkBtn();
        }
    }
}
@media (max-width:$xl){
    .portfolio-3 {
        padding-bottom: 80px;
    }
}
@media(max-width:$lg){
    .portfilio-3__item-sizer {
        width: calc(50% - 15px) !important;
    }
    .portfilio-3__item {
        width: 100% !important;
        &__nor,&__2h {
            width: calc(50% - 15px) !important;
        }
        &__2w {
            width: 100%;
        }
    }
}
@media (max-width:$md){
    .portfolio-3__grid {
        margin: 30px 0;
    }
}
@media(max-width:$sm) {
    .portfilio-3__item-sizer {
        width: 100% !important;
    }
    .portfilio-3__item {
        width: 100% !important;
        max-height: 420px;
    }
    .portfolio-3 {
        padding-bottom: 50px;
    }
}