.footer-3 {
    text-align: center;
    padding: 160px 0;
    a {
        &:hover {
            color: $white;
        }
    }
    h5 {
        font-family: $font-second-medium;
        
    }
    h4 {
        font-family: $font-second-semibold;
        font-size: 50px;
        margin: 10px 0;
    }
    h3{
        font-family: $font-primary;
    }
}
@media (max-width:$md){
    .footer-3 {
        padding: 80px 0;
        h5 {
            font-size: 20px;
        }
        h4 {
            font-size: 30px;
        }
        h3{
           font-size: 20px; 
        }
    }
    
}
@media (max-width:$sm){
    .footer-3 {
        h5 {
            font-size: 18px;
        }
        h4 {
            font-size: 22px;
            margin: 0;
        }
        h3{
           font-size: 18px; 
        }
    }
    
}