
// Lightbox requirements
.lightbox-enabled {
    & + header {
        visibility: visible !important ;
        opacity: 1 !important;
        margin-top: 0 !important;
    }

}
.speaker-detail {
    background: $main-dark;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    overflow: hidden;
    z-index: 10;   
    padding-top: 200px;
    
    .flickity-enabled {
        position: relative;
        
        &:focus { 
            outline: none;
        }
        
        &.flickity-enabled.is-draggable {
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none; 
        user-select: none; 

              
            // .flickity-viewport {
            //     cursor: move;
            //     cursor: -webkit-grab;
            //     cursor: grab;
            // }
            
            // .flickity-viewport.is-pointer-down {
            //     cursor: -webkit-grabbing;
            //     cursor: grabbing;
            // }
        }
        
        .flickity-viewport {
            overflow: hidden;
            position: relative;
            width: 100%;
            height: 100% !important;
        }
        
        .flickity-slider {
            position: absolute;
            width: 100%;
            height: 100%;

            // specitial fix
            .lightbox-container {
                &:nth-child(7) {
                    .speaker-detail__thumb {
                        img {
                            object-fit: contain;
                        }
                    }
                }
            }
        }
        .is-selected {
            .speaker-detail__info {
                @include filter(brightness(100%));
            }
            .speaker-detail__thumb {
                img {
                    filter: grayscale(0);
                }
   
            }
        }

    }
    .slider {
        width: 100%;
        height: 100%;


    }

// styling for lightbox
    
    .exit {
        position: absolute;
        bottom: 40px;
        left: 50%;
        @include transform(translate(-50%,0)); 
        cursor: pointer;
        display: inline-block;
        text-align: center;
        .more-profile {
            overflow: hidden;
            position: absolute;
            color: transparent;
            transition: color 0s 0.3s;
            @include fontface($font-second-semibold,25px,transparent,-40px 0 0);
            &::before {
                position: absolute;
                content: "";
                top: 7px;
                height: 20px;
                width: 100%;
                z-index: 2;
                @include transform(translateX(-105%));
                background-color: $white;
                -webkit-transition:transform 0.6s cubic-bezier(0.7,0,0.3,1);
                transition: transform 0.6s cubic-bezier(0.7,0,0.3,1);
            }
            &:hover {
                &::before {
                    @include transform(translateX(105%));
                }
                color: $white;
            }
        }
        .explosed {
            display: inline-block;
            @include fontface($font-second-semibold,25px,$dove-gray,0);
            span {
                display: inline-block;
            }

        }

    }
    
    .prev, .next {
        position: absolute;
        top: 47%;
        @include transform(translate(0,-50%)); 
   
    }
    
    .prev {
        left: 100px;
        
        @include transform(rotate(-90deg)); 

    }
    
    .next {
        right: 100px;
        @include transform(rotate(90deg)); 

    }
    
}


.speaker-detail__thumb {
    height: 570px;
    img {
        height: 100%;
        width: 100%;
        object-fit: cover;
        @include filter(grayscale(100%));
    }
}
.speaker-detail__info {
    @include filter(brightness(0.1));
    padding: 45px 0px 45px 70px;
    h4 {
        line-height: 60px;
        font-size: 60px;
        color: $white;
    }
    h5 {
        @include fontface($font-second-semibold,30px,$dusty-gray,20px 0 40px);
    }
    p {
        font-size: 22px;
        color: $dusty-gray;
    }
    .speaker-media {
        margin: 32px 15px 0 0;
        display: inline-block;
        &:hover {
            img {
                @include filter(brightness(0) invert(1)) ;
            }
        }
    }
}
///========================= sepaker Arrow
.speaker-slide-arrow {
    font-size: 50px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    position: relative;
    text-align: center;
    overflow: hidden;
}
/* Mallki */
.link--mallki {
	color: $dove-gray;
    font-family: $font-second;
    font-size: 25px;
	-webkit-transition: color 0.5s ;
	transition: color 0.5s;
	overflow: hidden;
}

.link--mallki:hover {
	-webkit-transition: none;
	transition: none;
	color: transparent;
	font-weight: 700;
}

.link--mallki::before {
	content: '';
	width: 100%;
	height: 2px;
	margin: -3px 0 0 0;
	background: #fff;
	position: absolute;
	left: 0;
	top: 50%;
	-webkit-transform: translate3d(-102%,0,0);
	transform: translate3d(-102%,0,0);
	-webkit-transition: -webkit-transform 0.4s;
	transition: transform 0.4s;
	-webkit-transition-timing-function: cubic-bezier(0.7,0,0.3,1);
	transition-timing-function: cubic-bezier(0.7,0,0.3,1);
}

.link--mallki:hover::before {
	-webkit-transform: translate3d(101%,0,0);
	transform: translate3d(101%,0,0);
}

.link--mallki span {
	position: absolute;
	height: 50%;
	width: 100%;
	left: 0;
	top: 0;
	overflow: hidden;
}

.link--mallki span::before {
	content: attr(data-letters);
	position: absolute;
	left: 0;
	width: 100%;
	color: #fff;
	-webkit-transition: -webkit-transform 0.5s;
	transition: transform 0.5s;
}

.link--mallki span:nth-child(2) {
	top: 48%;
}

.link--mallki span:first-child::before {
	top: 0;
	-webkit-transform: translate3d(0,100%,0);
	transform: translate3d(0,100%,0);
}

.link--mallki span:nth-child(2)::before {
	bottom: 0;
	-webkit-transform: translate3d(0,-100%,0);
	transform: translate3d(0,-100%,0);
}

.link--mallki:hover span::before {
	-webkit-transition-delay: 0.3s;
	transition-delay: 0.3s;
	-webkit-transform: translate3d(0,0,0);
	transform: translate3d(0,0,0);
	-webkit-transition-timing-function: cubic-bezier(0.2,1,0.3,1);
	transition-timing-function: cubic-bezier(0.2,1,0.3,1);
}

@media (max-width:1752px) {
    .speaker-detail {
        padding-top: 160px;
        .prev {
            left: 5px;
        }
        .next {
            right: 5px;
        }
        .exit {
            bottom: 30px ;
        }
    }
    .speaker-detail__thumb {
        width: 80%;
        height: 400px;
        margin: 0 auto;
    }

    .speaker-detail__info {
        padding-left: 0;
        h4 {
            margin-right: 5px;
            display: inline-block;
            font-size: 50px;
            line-height: 50px;
        }
        h5 {
            font-size: 35px;
            line-height: 35px;
        }
    }
}
@media (max-width:$xl){
    .speaker-detail  {
        position: absolute;
        height: 800px;
    }
    .prev {
        left: 20px !important;
    }
    .next {
        right: 20px !important;
    }
    .exit {
        bottom: 50px !important;
    }

}
@media (max-width:$lg){
    .speaker-detail  {
        padding-top: 150px;
        height: 700px !important;

        .prev {
            left: -5px !important;
        }
        .next {
            right: -5px !important;
        }
        .speaker-detail__thumb {
            height: 400px;
            width: 100%;
        }
        .speaker-detail__info {
  
            h4 {
                margin-right: 5px;
                display: inline-block;
                font-size: 35px;
                line-height: 35px;
            }
            h5 {
                font-size: 25px;
                line-height: 25px;
                margin: 20px 0;
            }
            p {
                font-size: 15px;
            }
        }
    }


}



@media (max-width:767px){
    .speaker-detail  {
        height: 1000px !important;
    }
}

@media (max-width:$sm){

    .speaker-detail__thumb {
        height: 400px;
    }
    .speaker-detail__info {
        padding: 30px 0;

    }
    

}












