.footer-1 {
    padding: 170px 0 195px;
    text-align: center;
    h2 {
        @include fontface ($font-second-light,40px,$white,0);
    }
    h1 {
        @include fontface ($font-second-bold,50px,$white,0);
        a {
            font-family: $font-second-semibold;
            color: #808080;
            border-bottom: 3px solid #808080;
            line-height: 42px;
            display: inline-block;
            &:hover {
                color: $white;
                font-family: $font-second-bold;
                border-bottom: 3px solid $white;
            }
        }
    }
}    
.copy-right__mid{
    color:$dusty-gray;
    span {
        &:nth-child(2){
            border-left: 1px solid $nd-dark;
            border-right: 1px solid $nd-dark;
        }
        padding: 0 15px;
    }
}
@media(max-width:$xl){
    .copy-right__area--2 {
        height: 120px;
        flex-direction: column;
        justify-content: space-around;
    }
    .footer-1 {
        padding: 100px 0 115px;
        h2 {
            font-size: 35px;
        }
        h1 {
            font-size: 40px;
            line-height: 40px;
        }
    }
}
@media(max-width:$md){
    .copy-right__area--2 {
        padding: 5px 0 8px !important;
        height: 160px !important;
    }
    .copy-right__mid {
        span {  
            display: block;  
            padding: 0;
            &:nth-child(2){
                border: none;
            }
        }
        padding: 7px 0; 
    }
}
@media(max-width:$sm){

    .footer-1 {
        padding: 80px 0 95px;
        h2 {
            font-size: 25px;
        }
        h1 {
            font-size: 30px;
            a {
                line-height: 30px;
            }
        }
    }
}
