
.speaker-grid-2 {
    padding: 130px 0;
}
.speaker-gutter-sizer {
    width: 19.658%;
    position: absolute;
}
.speaker-grid-2__item {
    visibility: hidden;
    .speaker-infomation {
        display: none;
    }
    &:nth-child(1) {
        margin-top: 0px;
    }

    width: 40.171%;
    margin-top: 125px;
    text-align: center;
    &__thumb {
        &__overflow {
            overflow: hidden;
        }
        img {
            @include filter(grayscale(100%));
            @include transform(scale(1.05)); 
        }
    }
    h4 {
        margin-top: 50px;
        font-size: 30px;
    }
    

    .speaker-grid-2__item__thumb {
        &:hover {
            img {
                @include filter(grayscale(0%));
                @include transform(scale(1));     
            }
        }

    
    }
    
}

@media (max-width:$lg) {
    .speaker-grid-2 {
        padding: 60px 0;
    }
    .speaker-grid-2__item {
        width: 47.5%;
        margin-top: 60px;
        h4 {
            margin-top: 25px;
        }
    }
    .speaker-gutter-sizer {
        width: 5%;
    }
}

@media (max-width:$md) {
    .speaker-grid-2 {
        padding: 40px 0;
    }
    .speaker-grid-2__item {
        margin-top: 30px;
        h4 {
            font-size: 20px;
            margin-top: 15px;
        }
        h5{
            font-size: 15px;
        }
    }

}


@media (max-width:$sm) {

    .speaker-grid-2__item {
        width: 80%;
        left: 50% !important;
        @include transform(translate(-50%,0) !important);
    }

}