@include sectionTittle(schedule__tittle,115px 0 115px 0,center);

.schedule-tab {
    height: 140px;
    display: flex;
    &__item {
        &:first-child {
            border-left: 1px solid $border;
        }
        width: 33.333%;
        @include Center();
        flex-direction: column;
    
        h2 {
            transition: all 0.3s ease;
            font-size: 35px;
        }
        p {
            transition: all 0.3s ease;
            font-size: 22px;
            color: $main-dark;
        }
       
        border: 1px solid $border;
        border-left: unset;
        @include BrgFillTop($main-dark);
        &:hover {
            h2,p {
                color: $white;
            }
        }
    }

    .tab-active {
        background-color: $main-dark;
        h2,p{
            color: $white;
        }
    }
}
.tab-current {
    display: block !important;
}
.schedule-table {
    display: none;

    padding-top: 150px;
    &__greating {
        border-top: unset !important;
    }
    &__end {
        height: 130px !important;
    }
    h3 {
        font-size: 20px;
        margin-bottom: 7px;
    }
    &__time {
        h3 {
            margin: 0;
        }
        @include Center();
        flex-direction: column;
        border-right: 1px solid $border;
        border-top: 1px solid $border;
        p {
            color: $main-dark;
            font-size: 16px;
        }
        height: 100%;
    }
    &__event {
        padding: 35px 50px 35px 40px;
        border-top: 1px solid $border;
        border-right: 1px solid $border;
        height: 100%;
        width: calc(100% - 15px);
        margin-left: -20px;
    }


    &__speaker {
        padding:40px 0 35px 40px;
        border-top: 1px solid $border;
        width: calc(100% + 65px);
        margin-left: -65px;

      
        &__name {
            margin-left: 15px;
            p{
                color: $nd-dark;
                font-family: $font-primary-semibold;
            }
            span {
                color: $dove-gray;
            }
        }

        &__info {
            &:first-child {
                margin-top: 0px;
            }
            display: inline-flex;
            align-items: center;
            margin-top: 12px;
        }
    }




}
.break-time {
    &--lunch {
        @include brgImg('../images/break-time2.png');
    }
    @include brgImg('../images/break-time1.png');
    height: 145px;
    &__coffee {
        padding: 35px 0 0 20px;
    }
    & > .row {
        height: 100%;
    }
    .schedule-table__time {
        border: unset;
    }
    & + .row {
        .schedule-table__time,.schedule-table__event,.schedule-table__speaker
        {
            border-top: unset !important;
        }
    }
}


@media(min-width:$xl){
    .schedule-table__time {
        width: 280px;
    }

    .schedule-table__greating--event {
        padding-right: 100px;
    }
}
@media(max-width:$xl){
    .schedule-table {
        padding-top: 60px;
    }
    .schedule-table__event {
        margin-left: -30px;
        width: calc(100% + 15px);
    }

}
@media(max-width:$lg){
    .schedule-table__event {
        width: calc(100% + 30px);
        padding: 30px;
        border-right: unset;
    }
    .schedule-table__speaker {
        width: 100%;
        margin: 0;
        padding: 15px;
        display: flex;
        justify-content: flex-start;
        flex-wrap: wrap;
        border-top: unset;
    }
    .schedule-table__speaker__info {
        margin:15px;
        &:first-child {
            margin-top: 15px;
        }
    }
    .schedule-tab {
        h2 {
            font-size: 30px;
        }
        p {
            font-size: 18px;
        }
    }
}
@media(max-width:$md){
    .schedule-table {
        padding-top: 40px;
    }
    .schedule-tab {
        height: 100px;
        h2 {
            font-size: 25px;
        }
        p {
            font-size: 14px;
        }
    }
}
@media(max-width:$sm){
    .schedule-table {
        .row {
            &:first-child {
                .schedule-table__time {
                    padding-top: 15px;
                }
            }
        }
    }
    .schedule-table__time {
        border-right: unset;
        padding: 30px 0 15px 0;

    }
    .schedule-table__event {
        width: 100%;
        padding: 0 0 15px 0;
        border: unset;
        margin: 0;
    }
    .schedule-table__speaker {
        padding: 0;
    }
    .break-time {
        height: 170px;
    }
    .break-time__coffee {
        padding: 0 0 0 15px;
        text-align: center;
        h3 {
            margin: 0;
            line-height: 20px;
        }
    }


    .schedule-tab {
        border-top: 1px solid $border;
        flex-wrap: wrap;
        height: auto;
        .schedule-tab__item {
            width: 100%;
            height:80px;
            border-left: 1px solid $border;
            border-top: unset;
        }
    }
}