
.portfolio-2__head {
    padding: 145px 0;
}

.portfolio-2__tittle {
    &__slogan {
        display: flex;
    }
    .link-draw__hover {
        margin: 43px 0 0 100px;
    }
    @include HandDrawCir(link-draw__hover,220px,-80px,-30px);
    a {
        @include LinkBtn();
    }
    hr {
        flex-shrink: 0;
        margin: 35px 30px 0 0;
        display: inline-block;
        width: 70px;
        height: 5px;
        background-color: $main-dark;
        
    }
    h2 {
        display: inline-block;
        font-size: 60px;
        line-height: 75px;
    }
    &__text {
        font-size: 20px;
    }
}
 
.portfolio-2-sizer {
    width: calc(33.33333%);
    height: 550px;
}

.portfolio-2__item {
    
    display: block;
    width: 31.939605%;
    &__h {
        height: 360px;
    }
    &__v {
        width: 360px;
        height: 550px;
    }
    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    &__category {
        display: none;
    }
}
.portfolio-2__img-fade {
    img {
        @include filter(opacity(0.4));
    }
}
.portfolio-2__cate-follow {
    width: 100%;
    position: fixed;
    pointer-events: none;
    h5{
        display: inline-block;
        background-color: $main-dark;
        color: $white;
        height: 50px;
        padding: 0 15px;
        line-height: 55px;
        margin-bottom: -10px;
    }
    p {
        background-color: $main-dark;
        display: inline-block;
        color: #cccccc;
        padding: 2px 15px 15px;
        line-height: 0px;
        height: 23px;
    }
    
}
.portfolio-2__view {
    margin: 125px 0 147px;
    text-align: center;
    @include HandDrawCir(portfolio-2__view__link,230px,-80px,-30px);
    a {
        @include LinkBtn();
    }
   
}

@media(min-width:1752px) {

    .portfolio-2__tittle__text {
        &1 {
            margin-left: -30px;
        }
        &2 {
            padding-left: 40px;
        }
    }


}
@media (max-width:$xl) {
    .portfolio-2__head {
        padding: 80px 0;
    }
    .portfolio-2__tittle__text {
        margin-top: 40px;
    }
    .portfolio-2__view {
        margin: 80px 0 !important;
    }
    .portfolio-2-sizer {
        height: 350px;
    }
    .portfolio-2__item {
        &__h {
            height: 220px;
        }
        &__v {
            width: 220px;
            height: 350px;
        }
    }
}

@media (max-width:$md){
    .portfolio-2__view {
        margin: 0 0 50px !important;
    }
    .portfolio-2__tittle {
        h2{
            font-size: 40px !important;
            line-height: 50px !important;
        }
        hr {
            width: 50px !important;
            margin: 25px 30px 0 0 !important;
        }
    }
    .portfolio-2-sizer {
        width: 100%;
        height: 300px;
    }
    .portfolio-2__item {
        width: 80%;
        max-height: 400px;
    }
    .portfolio-2 {
        margin-top: -40px;
    }
}
@media (max-width:$sm){
    .portfolio-2__head {
        padding: 50px 0;
    }
    
    .portfolio-2__tittle a {
        margin: 30px 0 0 50px !important;
    }
    .portfolio-2__tittle {
        h2{
            font-size: 30px !important;
            line-height: 40px !important;
        }
        &__text {
            margin-top: 30px !important;
            font-size: 18px !important;
        }
        hr {
            width: 30px !important;
            margin: 20px 15px 0 0 !important;
        }
    }
}