.video {
    margin-top: 120px;
    position: relative;
    &__brg {
        min-height: 300px;
        object-fit: cover;
    }
}
.video__btn {
    position: absolute;
    @include transform(translate(-50%,-50%));
    left: 50%;
    top: 50%;
    &:hover {
        .video__btnIner {
            left: 0;
            @include transform(scale(1));
        }
    }
    .video__btnIn {
        position: relative;
    }
    .video__btnIner {
        transition: all 0.3s ease-in-out;
        position: absolute;
        left: -4px;
        top: 0px;
        @include transform(scale(0.7));
        width: 0;
        height: 0;
        border-top: 45px solid transparent;
        border-left: 90px solid $white;
        border-bottom: 45px solid transparent;
    }
}
.preview-video {
    visibility: hidden;
    position: fixed;
    z-index: 100;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: rgba($color: $main-dark, $alpha: 0.9);
    &__close {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
    }
    &__btn-close {
        position: absolute;
        top: -40px;
        right: -10px;
        color: $white;
        font-size: 30px;
    }
    &__area {
        position: relative;
        left: 50%;
        top: 50%;
        @include transform(translate(-50%,-50%));
        width: 70%;
        height: 60%;
        min-height: 300px;
        iframe {
            width: 100%;
            height: 100%;
        }
    }
    @media (max-width:$md){
        &__area {
            width: 90%;
        }
    }
}
@media (max-width:$xl){
    .video {
        margin-top: 60px;
    }
}
@media (max-width:$sm){
    .video {
        margin-top: 20px;
    }
}