.pagebanner-3 {
    margin-top: -100px;
    height: 910px;
    width: 100%;
    @include brgImg('../images/uploads/pagebanner/creative-agency.jpg'); 
    @include Center();
    &__text {
        h1 {
            text-align: center;
            line-height: 120px;
            &:nth-child(1){
             
                -webkit-text-stroke-width: 1.5px;
                -webkit-text-stroke-color: $white;
                @include fontface($font-primary-extrabold,90px,transparent,0);
            }
            &:nth-child(2) {
                @include fontface($font-primary-bold,120px,$white,0);
            }
        }
    }
}

@media (max-width:$lg){
    .pagebanner-3 {
        height: 700px;
    }
    .pagebanner-3__text {
        h1 {
            line-height: 80px;
            &:nth-child(1){
                font-size: 60px !important;
            }
            &:nth-child(2){
                font-size: 80px !important;
            }
        }
    }
}

@media (max-width:$md){
    .pagebanner-3 {
        height: 500px;
    }
    .pagebanner-3__text {
        h1 {
            line-height: 60px;
            &:nth-child(1){
                font-size: 50px !important;
            }
            &:nth-child(2){
                font-size: 60px !important;
            }
        }
    }
}