@include sectionTittle(schedule-list__tittle,135px 0 55px,center);
.schedule-list {
    &--lower {
        margin: 90px 0 0 70px;
    }
    h2 {
        text-align: center;
        margin-bottom: 35px;
    }
}
.schedule-list__box {
    &--lower {
        padding-left: 70px;
        ul {
            padding: 0 0 0 30px !important;
        }
    }
    display: flex;
    h3 {
        writing-mode: vertical-rl;
        @include transform(rotate(180deg));
        @include fontface($font-primary-bold,74px,$silver,0);
        color: $silver;
        line-height: 70px;
        word-spacing: -3px;
        letter-spacing: -1px;
        margin-bottom: 5px;
  
    }
    ul {
        width: 100%;
        padding: 0 40px 0 30px;
    }
    li{
    
        &:first-child {
            margin-top: 0px;
        }
        margin-top: 20px;
        display: flex;
        justify-content: space-between;
        span {
            font-size: 20px;
            color: $nd-dark;
            font-family: $font-primary-medium;
        }
    }
    
}

@include MQ(XL) {

    .schedule-list__box {
        ul {
            padding: 0 0 0 15px !important;
            span {
                font-size: 18px;
            }
        }
    }
    .schedule-list__box--lower {
        padding: 0;
    }
}
@include MQ(L) {
    .schedule-list {
        h3 {
            font-size: 70px;
        }
        .schedule-list--lower {
            margin: 40px 0 35px 0px;
        }
    }
    .schedule-list__box {
        padding: 0 70px;
    }
}
@include MQ(M) {
    .schedule-list__box {
        padding: 0;
    }
}
@include MQ(S) {
    .schedule-list {
        h3 {
            font-size: 50px;
            line-height: 50px;
        }
        h2 {
            font-size: 25px;
            margin-bottom:20px; 
        }
    }
    .schedule-list__box {
        ul {
            li {
                margin-top: 10px;
            }
            span {
                font-size: 15px;
            }
        }

    }
}