.testi-2 {
    padding: 100px 0;
    @include brgImg('../images/uploads/testi/testi-bg.jpg');
    width: 100%;
    height: 900px;
    .portfolio-2__tittle__slogan h2 {
        color: $white;
    }
    .portfolio-2__tittle__cross {
        background-color: $white;
    }
}
.testi-2__content {
    padding-left: 100px;
    max-width: 675px;
    p {
        font-family: $font-primary-light;
        color: $border;
        font-size: 20px;
        margin: 30px 0;
    }
    &__achievement {
        border-top: 1px solid $border;
        &:nth-of-type(1) {
            border-top: 1px solid transparent;
        }
        padding: 35px 0;
        & > div {
            h3 {
                font-size: 28px;
                color: $white;
            }
            width: 170px;
        }
        display: flex;
        align-items: center;
        ul {
            margin-top: -10px;
        }
        li {
            font-size: 20px;
            color: $white;
            margin-top: 10px;
            list-style-type: none;
        }
    }
}
.testi-2__qoute {
    padding: 135px 0;
    .testimonial-area {
        margin-top: 80px;
    }
    @media (max-width:$md) {
        .testimonial-area {
            margin-top: 30px;
        }
    }
}
.testi-2__qoute__tittle {
    @extend .portfolio-2__tittle;
}
@media(max-width:$xl){
    .testi-2__qoute {
        padding: 80px 0;
    }
}
@media (max-width:767px){
    .testi-2__content {
        padding-left: 50px;
        p,li {
            font-size: 15px;
        }
        &__achievement > div {
            width: 130px;
        }
    }
    .testi-2__qoute {
        padding: 50px 0;
    }
}
@media (max-width:$sm){
    .testi-2__content {
        padding-left: 0px;
        &__achievement > div {
            width: 100px;
        }
    }
}