.comment__item {
    display: flex;
    margin-top: 20px;
    &__thumb {
        width: 70px;
        height: 70px;
        flex-shrink: 0;
    }
    &__text {
        padding-left: 30px;
        &__rep {
            font-size: 13px;
            color: $main-dark !important;
            .fa-reply{
                margin-right: 5px;
            }
        }
    }

    h3 {
        display: inline-block;
        font-size: 15px;
    }
    span {
        @include fontface($font-primary,14px,$dusty-gray,0 0 0 25px);
    }
    p {
        margin: 10px 0 15px;
    }
}
.comment {
    padding: 60px 0 0;
    h5 {
        font-size: 25px;
        margin-bottom: 30px;
    }   
    &__leave-comment {
        margin-top: 50px;
    }


}



@media (max-width:$md){
    .comment {
        padding-top: 40px ;
        h5 {
            margin-bottom: 15px;
        }

    }
    .comment__leave-comment {
        margin-top: 30px;
    }
}
@media (max-width:$sm){
    .comment {
        h5 {
            font-size: 20px;
        }
    }
}