.pagebanner-2 {
    margin-top: -100px;
}

.pagebanner-brg {
    height: 910px;
    width: 100%;
    padding-top:175px;
    &--1 {
        @include brgImg('../images/uploads/pagebanner/pagebanner2-1.jpg'); 
        &__thumb {
            display: block;
            width: 100%;
            text-align: center;  
            img {
                display: inline-block;
            }
        }
        h5 {
            color: $white;
            font-size: 25px;
            font-family: $font-second-medium;
        }
        h2 {
            line-height: 80px;
            @include fontface($font-second-light,80px,$white,30px 0 0 0);
        }
        h3 {
           line-height: 85px;
            @include fontface($lato-medium,85px,transparent,0);
            @include brgImg('../images/uploads/pagebanner/textbrg.jpg');
            background-repeat: repeat-y;
            @include animation(animation,slide 8s linear infinite);
            @include keyframe(slide){
                from { background-position: 0 0; }
                to { background-position: 0 100%; }
            }
            -webkit-background-clip: text;  
            background-clip: text;
        }
        @media(max-width:$xl){
            .pagebanner-brg--1__right {
                padding:30px 0 0 ;
            }
        }
        @media(max-width: $lg){
            &__thumb {
                img {
                    max-width: 400px;
                }
            }
            h2 {
                font-size: 60px;
            }
            h3 {
                line-height: 55px;
                font-size: 55px;
            }
        }
        
    }
    &--2 {
        padding-top: 0 !important;
        @include Center();
        text-align: center;
        @include brgImg('../images/uploads/pagebanner/pagebanner2-2.jpg');
        h1 {
            text-transform: uppercase;
            @include fontface(myriad,170px,transparent,0);
            @include brgImg('../images/uploads/pagebanner/textbrg2.jpg');
            @include animation(animation,slide 20s linear infinite);
            @include keyframe(slide){
                0% { background-position: 0 0; }
                100%  { background-position: 0 100%; }
            }
            -webkit-background-clip: text;  
            background-clip: text;
            line-height: 170px;
        } 
    }
    &--3 {
        & > .extra-container {
            & > .row {
                height: 100%;
            }
            height: 100%;
        }
        &__left {
            & > img {
        
                position: absolute;
                top: -70px;
                left: -140px;
            }
            position: relative;
            max-width: 660px;
            float: right;
            padding: 45px 0 0 ;
            a {
                @include BtnSolid();
            }
        }
        &__right {
            height: 100%;
            position: relative;
            img {
                position: absolute;
                bottom: 1px;
                left: 100px;
            }
        }
        @extend .pagebanner-brg--1;
        @include brgImg('../images/uploads/pagebanner/pagebanner2-3.jpg'); 
    }
}


.pagebanner-brg--1__right {
    padding: 45px 0 0 70px;
    a {
        @include BtnSolid();
        width: unset;
        padding-left: 20px;
        padding-right: 20px;
    }
}
.pagebanner__count-down-area {
    margin: 40px 0 50px;
    max-width: 450px;
    display: flex;
    justify-content: space-between;

}

.pagebanner__count-down {
    display: inline-block;
    text-align: center;
    p {
        line-height: 60px;
        @include fontface($lato-medium,50px,$white,0);
    }
    span {
        @include fontface($lato,20px,$dusty-gray,0);
    }

}
@media(max-width:1751px){
    .pagebanner-brg--3__right {
        img {
            left: 0;
        }
    }
    .pagebanner-brg--3__left{
        img {
            left: 0px;
        }
    }
    .pagebanner-brg--2 {
        h1 {
            font-size: 140px !important;
        }
    }
}
@media(max-width:$lg){
    .pagebanner-brg {
        max-height: 650px;
    }
    .pagebanner-brg--3__right, .pagebanner-brg--1__thumb {
        display: none;
    }
    .pagebanner-brg--3 {

        &__left {
            width: 100%;
            max-width: 550px;
        }
        .row {
            align-items: center;
        }
    }
    .pagebanner-brg--2 {
        h1 {
            font-size: 100px !important;
            line-height: 100px !important;
        }
    }
    .pagebanner__count-down-area {
        margin: 30px 0;
    }
    .pagebanner-brg {
        padding-top: 140px;
    }
}

@media (max-width:$md){
    .pagebanner-brg--2 {
        h1 {
            font-size: 60px !important;
            line-height: 80px !important;
        }
    }
}
@media (max-width:$sm){
    .pagebanner-brg {
        max-height: 600px;
    }
    .pagebanner-brg--2 {
        h1 {
            font-size: 40px !important;
            line-height: 50px !important;
        }
    }

    .pagebanner-brg--1__right {
        padding-top: 0 !important;
    }
    .pagebanner-brg--1__info {
        h2,h3 {
            line-height: 45px !important;
            font-size: 40px !important;
        }
    }
    .pagebanner-brg--3__left{
        padding: 0;
        img {
            max-height: 320px;
            top: -30px;
        }
    }
    .pagebanner__count-down {
        p {
            font-size: 35px !important;
        }
        span {
            font-size: 15px;
        }
    }

}