footer {
    background-color: $main-dark;
}

.footer {
    padding: 110px 0 100px;
    text-align: center;
    &__logo {
        img {
            opacity: 0;
            margin-bottom: 30px;
        }
     
    }
    p {
        color: $white ;
    }

    &__site-text {
        p {
            font-size: 18px;
        }

    }

    &__get-ticket {
        text-align: center;
        a {
            @include BtnSolid();
            margin: 50px 0;
        }

    }

    &__menu {
        color: $white;
        text-align: center;
        li {
            list-style-type: none;
            display: inline-block;
            margin: 0 30px;
        }
        a {
            color: $dusty-gray;
            &:hover {
                color: $white;
            }
        }
    }
}

.copy-right {
    border-top: 1px solid $dove-gray;
    &__area {
        height: 77px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__left {
        color: $dusty-gray ;
        span {
            color: $white !important;
        }
    } 

    &__right {
 
        width: 150px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        img {
          
            &:hover {
                -webkit-filter:brightness(0) invert(1);
                filter: brightness(0) invert(1); 
            }
        }
    }

}

@include MQ(M) {
    .footer {
        padding: 80px 0 60px;
    }
    .footer__menu {
        li {
            display: block;
            margin: 0 0 5px 0;
        }
    }
    .copy-right {
        .copy-right__area {
            text-align: center;
            height: 100px;
            padding:  5px 0;
            flex-wrap: wrap;
            justify-content: center;
        }
    }
}