@import 'menu-nav';
header {
    position: fixed;
    background-color: $main-dark;
    height: 100px;
    width: 100%;
    z-index: 100;
    transition: all 0.5s;
}
.header-hide {
    visibility: hidden;
    transition: all 0.8s;
    opacity: 0;
    margin-top: -100px;

}

.header-posistion {
    height: 100px;
    background-color: $main-dark;
}
.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height:100px;
    &__logo {
        margin-left: 100px;
    }
    &__buy-ticket {
        @include BtnSolid(); 

    }
        
}


.header__menu-icon{

    position: relative;
    width: 23px;
    height: 23px;
    &::before {
        content: "";
        background-color: $white;
        border-radius: 50%;
        position: absolute;
        transition: 0.8s;
        width: 0;
        height: 0;
        left: 50%;
        top: 50%;
        @include transform(translate(-50%,-50%));
    }
    &__dot {
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        position: absolute;
        span {
            left: 8px;
            top:8px ;
            transition: 0.5s ease-out;
            position: absolute;
            display: block;
            width: 7px;
            height: 7px;
            background-color: $white;
            border-radius: 50%;
            @include boxShadow(-9px 0px 0px 0px $white);
            &:nth-child(2) {
                @include boxShadow(9px 0px 0px 0px $white);
            }
        }
        &--vertical {
            @include transform(rotate(90deg));
        }
        &--skew {
            @include transform(rotate(45deg));
            span {
                @include boxShadow(-12.6px 0px 0px 0px $white );
                &:nth-child(2) {
                    @include boxShadow(12.6px 0px 0px 0px $white );
                }
            }
        }
        &--skew-reverse {
            @extend .header__menu-icon__dot--skew;
            @include transform(rotate(-45deg));
        }
    }

    &:hover {
        &::before {
            width: 15px;
            height: 15px;
        }
        span {
            @include boxShadow(0px 0px 0px 0px $white);
        }

    }

}

// header-home(header-2)
.header-2 {
    @extend .header;
    .header__buy-ticket {
        
        @include BtnTrans();
        
    }
}


@include MQ(M) {
    .header__logo {
        margin-left: 0;
    }

    .header__buy-ticket {
        width: 100px !important;
        font-size: 13px;
        height: 40px !important;
        line-height: 42px !important;
    }

}



