
.experience__tittle {
    overflow: hidden;
    padding: 130px 0 130px;
    @include sectionTittle(experience__tittle1,0,left);
    @include sectionTittle(experience__tittle2,0,right);
    h1 {
        display: inline-block;  
    }

    h3 {
        text-align: center;
        @include fontface($font-primary-bold,170px,$main-dark,0);
        line-height: 285px;
    }
    .experience__tittle1 h1 {
        @include animation(animation,scroll-left 10s linear infinite);
    }
    .experience__tittle2 h1 {
        @include animation(animation,scroll-right 10s linear infinite);
    }
}

.experience {
    .row {
        &:nth-child(1){
            padding: 0 275px;
        }
        &:nth-child(2){
            margin-top: -40px;
        }

        &:nth-child(3){
            & > div {
                margin: 0 auto;
            }
        }
    }
    &__thumb {
        display: block;
        overflow: hidden;
        img {
            @include filter(grayscale(100%));
        }
        &:hover {
            img {
                @include filter(grayscale(0%));
                @include transform(scale(1.05));     
            }
        }

        &--left {
            float: left;
        }
        &--right {
            float: right;
        }
        &--middle {
            float: right;
            margin-top: 100px;
        }

    }
    &__text {
        text-align: center;
        width: 77%;
        margin: 85px auto 0;
        p {
            @include fontface($font-primary,22px,$nd-dark,25px 0 40px);
        }
        a {
            @include LinkBtn();
    
        }
        @include HandDrawCir(link-draw__hover,180px,-60px,-30px);
    }
}

#counting {
    .letter {
        display: inline-block;
    }
}
.movein {
    transform: translate(50%,50%);
}

@media(max-width:1752px){
    @include keyframe(scroll-left) {
        0%  { 
            @include transform(translateX(140%)); 		
        }
        100% { 
            @include transform(translateX(-100%));
        }
    }
    @include keyframe(scroll-right) {
        0%  { 
            @include transform(translateX(-100%));
        }
        100% { 
            @include transform(translateX(140%));
        }
    }
    .experience {
        .row {
            &:nth-child(1){
                padding: 0 70px;
            }
        }
    }
    .experience__text {

        h1 {
            font-size: 60px;
        }
        p {
            margin: 20px 0;
            font-size: 20px;
        }
        margin: 55px auto 40px;
    }
    .experience__thumb--left,.experience__thumb--middle {
        width: calc(100% + 35px);
    }
    .experience__thumb--right {
        img {
            max-height: 400px;
        }   
      
    }

}




.experience__tittle {
    @media(min-width:1752px){
        @include keyframe(scroll-left) {
            0%  { 
                @include transform(translateX(180%)); 		
            }
            100% { 
                @include transform(translateX(-100%));
            }
        }
        @include keyframe(scroll-right) {
            0%  { 
                @include transform(translateX(-100%));
            }
            100% { 
                @include transform(translateX(180%));
            }
        }
    }

    @media(max-width:$xl) {

        padding: 60px 0;
        h3 {
            font-size: 100px !important;
            line-height: 70px !important;
        }

    }
    @media(max-width:1199px){
        @include keyframe(scroll-left) {
            0%  { 
                @include transform(translateX(100%)); 		
            }
            100% { 
                @include transform(translateX(-100%));
            }
        }
        @include keyframe(scroll-right) {
            0%  { 
                @include transform(translateX(-100%));
            }
            100% { 
                @include transform(translateX(100%));
            }
        }
    }
    @media(max-width:$lg) {
        padding: 0;
        h3 {
            font-size: 80px !important;
            line-height: 60px !important;
        }
    }
    @media(max-width:$md) {
        h3 {
            font-size: 60px !important;
            line-height: 40px !important;
        }
    }
    @media(max-width:$sm) {
        h3 {
            font-size: 40px !important;
            line-height: 30px !important;
        }
        .experience__tittle1{
            h1 {
                margin-bottom: 20px !important;
            }
        }
        .experience__tittle2{
            h1 {
                margin-top: 20px !important;
            }
        }
    }
}
@include MQ(XL){
    .experience__thumb--left,.experience__thumb--middle {
        width:100%;
    }
    .experience__text {
        width: 100%;
        h1 {
            font-size: 40px;
        }
        p {
            margin: 20px 0;
            font-size: 18px;
        }
    
    }
    .experience__thumb--right {
        img {
            max-height: 330px;
        }   
      
    }
    .experience {
        .row {
            &:nth-child(3){
                & > div {
                    .experience__thumb--center {
                        text-align: center;
                        margin-top: 35px;
                        img {
                            max-height: 300px;
                        }
                    }
                }
            }
            &:nth-child(2){
                margin-top: -60px;
            }
        }
    }
}

@media (max-width:$lg) {
    .experience {
        .row {
            &:nth-child(1){
                padding: 0px;
                .experience__thumb {
                    img {
                        height: 250px;
                        width: 100%;
                        object-fit: cover;
                    }
                }
            }
            &:nth-child(2){
                margin-top: 30px;
               .experience__thumb {
                
                   &--middle {
                       margin: 0;
                   }
                    img {
                        height: 250px;
                        width: 100%;
                        object-fit: cover;
                    }
                }
                & > div {
                    &:nth-child(2){
                        order: 1;
                    }
                }
                .experience__text {
                    margin: 40px 0;
                }
            }
    
            &:nth-child(3){
                & > div {
                    margin: 0 auto;
                }
                .experience__thumb--center {
                    margin-top: 0 !important;
                }
            }
        }
    }
    .experience__text {
        opacity: 1;
    }
}

@media (max-width:$sm){
    .experience__thumb {
        margin-top: 30px !important;
    }
    .experience__thumb--right {
        width: 100%;
    }
    .experience {
        .row {
            margin-top: 0 !important;
            &:nth-child(1){
                & > div {
                    &:first-child {
                        .experience__thumb {
                            margin-top: 0px !important;
                        }
                    }
                }
            }
        }
    }
    .experience__text {
        h1 {
            font-size: 30px;
        }
        p {
            font-size: 15px;
        }
    }
}




