@include sectionTittle(porfolio-single__tittle,150px 0 130px 0,center);
.portfolio-single {
    padding-bottom: 150px;
}
.porfolio-single__pictute {
    margin-bottom: 30px;
    text-align: center;
}
.portfolio-single__content {
    @media (min-width:$xl){
        padding:30px 0 0 50px;
    }
    h5 {
        font-size: 20px;
        font-family: $font-second-bold;
        margin-bottom: 20px;
    }
    p {
        &:first-child {
            margin-top: 0;
        }
        margin-top: 10px;
    }
    ul {
        border-top:1px solid $border;
        padding-top: 45px;
        margin-top:45px;
        li {
            list-style-type: none;
            color: $dusty-gray;
            margin-bottom:15px;
        }
        span {
            margin-right: 15px;
            font-family: $font-second-bold;
            color: $main-dark;
        }
        i{
            margin-right: 12px;
        }
    }
}
.portfolio-single__pagination {
    a {
        &:hover {
            h5,h6 {
                color: $main-dark;
            } 
        }
    }
    margin-top: 50px;
    display: flex;
    justify-content: space-between;
    height: 150px;
    align-items: center;
    border-top: 1px solid $border;
    border-bottom: 1px solid $border;
    h6,i {
        font-size: 18px;
    }
    .paging-btn {
        transition: all 0.3s ease-in-out;
        margin: 0 8px;
        display: inline-block;
        font-family: $font-second-semibold;
    }
    h5 {
        font-family: $font-second-bold;
        font-size:20px;
        transition: all 0.3s ease-in-out;
        margin-top: 10px;
    }
}

@media(max-width:$xl) {
    .portfolio-single {
        padding-bottom: 80px;
    }
}
@media (max-width:$md) {
    .portfolio-single__content {
        h5 {
            margin-top: 10px;
        }
        ul {
            margin-top: 30px;
            padding-top: 30px;
        }
    }
    .portfolio-single__pagination {
        margin-top: 30px;
    }
    .portfolio-single {
        padding-bottom: 50px;
    }
}